<template>
<div class="oss-file">
  <input type="file" :id="id" accept="image/png,image/jpeg,image/bmp,image/jpg" :multiple="multiple" @change="doUpload" />
</div>
</template>
<script>
// 
function getFileName(o) {
  var pos = o.lastIndexOf('.');
  return o.substring(pos, 0);
}
import {
  token
} from '@/common/api/api';
export default {
  /**
   * multiple 
   * id ID
   * fileType ___-101( banner)2()3         4 101banner102103()
   * maxNum 
   * maxSize 
   */
  props: {
    multiple: {
      type: Boolean,
      default: false
    },
    id: [String, Number],
    fileType: [String, Number],
    maxNum: Number,
    maxSize: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      dir: '',
      region: process.env.NODE_ENV === 'production' ? 'oss-cn-shenzhen' : 'oss-cn-shanghai',
      url: []
    };
  },
  methods: {
    /**
     * [UUID]
     */
    uuidMethod() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0,
          v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
    },
    /**
     * 
     * @param  {[type]} target []
     */
    doUpload(e) {
      let _ = this;
      const files = document.getElementById(_.id);
      // 
      const fileLen = files.files;
      // 1.
      if (this.maxNum && fileLen.length > this.maxNum) {
        this.$toast({
          type: 'error',
          message: `${this.maxNum}`
        });
        e.target.value = '';
        return;
      }
      for (let i = 0; i < fileLen.length; i++) {
        let file = fileLen[i];
        // 2.
        let size = Math.round(file.size / 1024 / 1024);
        if (size > this.maxSize) {
          this.$toast({
            type: 'error',
            message: `${this.maxSize}MB`
          });
          e.target.value = '';
          return;
        }
      };
      // 3.OSS token
      token(this.fileType).then((res) => {
        let data = res.data.data;
        _.dir = data.dir;
        var arr = [];
        _.url = [];
        if (fileLen.length > 0) {
          const client = new OSS.Wrapper({
            region: _.region,
            secure: process.env.NODE_ENV === 'production',
            accessKeyId: data.accessKeyId,
            accessKeySecret: data.accessKeySecret,
            stsToken: data.securityToken,
            bucket: data.bucket
          });
          _.$emit('begin');
          for (let i = 0; i < fileLen.length; i++) {
            let file = fileLen[i];
            // KEY
            let storeAs = _.dir + 'web-' + _.uuidMethod().split('-').join('') + '.' + file.name.split('.')[file.name.split('.').length - 1];
            arr.push(new Promise((resolve, reject) => {
              //1 =>oss
              client.multipartUpload(storeAs, file, {}).then((results) => {
                //2 
                //=> size,name,base64
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = (e) => {
                  _.url.push({
                    size: file.size,
                    name: (getFileName(file.name)).substring(29, 0),
                    result: e.target.result,
                    storeAs: storeAs
                  });
                  resolve(_.url);
                };
              }).catch((err) => {
                console.log(err);
              });
            }));
          }
          Promise.all(arr).then(urls => {
            e.target.value = '';
            this.$emit('doUpload', urls[urls.length - 1]);
          });
        }
      }).catch();
    }
  }
};
</script>
<style type="text/css" scoped>
.oss-file input {
  position: absolute;
  opacity: 0;
  filter: alpha(opacity=0);
}
</style>



